.container {
  width: 100vw;
  height: 100vh;
  background-color: aliceblue;
  overflow: hidden;
}
.first_scr{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(158, 177, 194);
  color: #060708;
  line-height: 2.3em;
  
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.main-text{
  font-size: calc(1.2rem + 10vmin);
  letter-spacing: 2px;
  
}
.mini-text{
  font-size: calc(1rem + 2vmin);
  letter-spacing: 2px;
  color: #282c34;
  color: rgb(56, 1, 1);
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
::-webkit-scrollbar{
  width: 4px;
  height: 2px;
  background-color: grey;
}
.tasks_wrapper{
  padding: 0 30px;
  padding-top: 60px; 

 
}
.section_title{
  font-size: 24px;
}
.items{

  max-height: 65vh;
  overflow-y: auto;
  overflow-x: hidden;
  
}
.item{
  
  background-color: #fff;
  padding: 20px;
  margin: 0 10px;
  margin-top: 30px;
  color: black;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item_left{
  display: flex;
  align-items: center;
}
.item:hover .circular{
  background-color: #61dafb;
}
.square{
  width: 20px;
  height: 20px;
  background-color: #61dafb;
}
.item_text{
  margin-left: 20px;
  font-weight: 500;
}
.circular{
  width: 15px;
  height: 15px;
  border: 3px solid #61dafb;
  border-radius: 20px;
}
.write_task_wrapper{
  position: absolute;
  padding-left: 30px;
  bottom: 40px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
 
}
.input{
  padding: 15px;
  width: 70%;
  
  border: 60px;
  border: 1px solid #C0C0C0;
  border-radius: 60px;
  background-color: white;
  transition: width 700ms cubic-bezier(0.175, 1, 0.32, 2);
}
 
.add_wrapper{
  width: 50px;
  height: 50px;
  background-color: white;
  border: 1px solid #C0C0C0;
  border-radius: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 4;
  transition: all 100ms cubic-bezier(0.175, 1, 0.32, 2);
}
.add_text{
  font-size: calc(100% + 8px);
  margin: auto;
  color: darkgreen;
 
}

.add_wrapper:active{
transform: translate(0, 2px);

z-index: 1;
}
.input:focus{
  width: 80%;
}
@media (prefers-reduced-motion: no-preference) {
  /*.App-logo {
    animation: App-logo-spin infinite 20s linear;
  }*/
  .main-text {
    animation: move-in-left 5s ease;
  }
  .mini-text {
    animation: move-in-right 5s ease;
  }
  .container {
    animation: fade-in 3s ease;
  }
}

#loader {
  position: fixed;
  width: 100%;
  height: 100%;
  background: none;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 0;
  display: none;

}

#loader.active {
  z-index: 100009999999999999;
  display: flex;
  align-items: center;
  justify-content: center;
}

#loader>div {
background-color: none;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  perspective: 800px;
}

.inner {
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.inner.one {
  left: 0%;
  top: 0%;
  animation: rotate-one 1s linear infinite;
  border-bottom: 3px solid #7474b6;
}

.inner.two {
  right: 0%;
  top: 0%;
  animation: rotate-two 1s linear infinite;
  border-right: 3px solid #7474b6;
}

.inner.three {
  right: 0%;
  bottom: 0%;
  animation: rotate-three 1s linear infinite;
  border-top: 3px solid #7474b6;
}



@keyframes rotate-one {
  0% {
      transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }

  100% {
      transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes rotate-two {
  0% {
      transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }

  100% {
      transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes rotate-three {
  0% {
      transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }

  100% {
      transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}


@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes move-in-left {
  from {
    transform: translateX(-100px);
    opacity: 0;
  }
  to {
    transform: translateX(0px);
    opacity: 1;
  }
}
@keyframes move-in-right {
  from {
    transform: translateX(100px);
    opacity: 0;
  }
  to {
    transform: translateX(0px);
    opacity: 1;
  }
}
